/*
$primary-color-dark:   #388E3C;
$primary-color:        #4CAF50;
$primary-color-light:  #C8E6C9;
$primary-color-text:   #FFFFFF;
$accent-color:         #FF4081;
$primary-text-color:   #212121;
$secondary-text-color: #727272;
$divider-color:        #B6B6B6;
*/
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url("https://fonts.gstatic.com/s/roboto/v15/2UX7WLTfW3W8TclTUvlFyQ.woff") format("woff"); }

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("https://fonts.gstatic.com/s/roboto/v15/d-6IYplOFocCacKzxwXSOD8E0i7KZn-EPnyo3HZu7kw.woff") format("woff"); }

html {
  background: #fafafa; }

html, body {
  margin: 0;
  padding: 0;
  font-family: roboto, sans-serif;
  -webkit-tap-highlight-color: transparent; }

pre,
code,
kbd,
samp,
tt {
  font-family: monospace;
  font-size: 1em; }

button:-moz-focus-inner {
  border: 0;
  padding: 0; }

button {
  cursor: pointer; }

.unbutton {
  display: inline-block;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  white-space: nowrap; }

.layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
  flex-flow: column;
  height: 100%; }

.main {
  -webkit-flex: 1;
  flex: 1;
  display: -webkit-flex;
  display: flex;
  overflow: hidden;
  position: relative; }

html, body {
  height: 100%;
  overflow: hidden; }

.scroller {
  overflow: hidden;
  overflow-y: scroll;
  width: 100%; }

.card {
  background: #fff;
  overflow: hidden;
  border-radius: 2px;
  margin: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); }
  @media (min-width: 600px) {
    .card {
      margin: 16px;
      box-shadow: 0 3px 7px rgba(0, 0, 0, 0.15); } }
  @media (min-width: 800px) {
    .card {
      margin: 16px auto;
      width: 756px; } }

.toolbar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  position: relative;
  z-index: 1;
  background: #3F51B5;
  color: #FFFFFF;
  height: 56px;
  box-shadow: 0 0px 11px rgba(0, 0, 0, 0.4); }
  @media (min-width: 600px) {
    .toolbar {
      height: 72px; } }
  .toolbar .site-title {
    font-size: 1.2rem;
    font-weight: normal;
    margin: 0;
    padding: 0;
    margin-left: 16px; }
    @media (min-width: 600px) {
      .toolbar .site-title {
        margin-left: 24px;
        font-size: 1.3rem; } }

.post-main-img {
  position: absolute;
  top: 0;
  width: 100%; }
  @supports (object-fit: cover) {
    .post-main-img {
      height: 100%;
      object-fit: cover; } }

.post-main-img-container {
  position: relative;
  overflow: hidden;
  background: #e4e4e4;
  transform: translateZ(0); }
  .post-main-img-container::before {
    content: '';
    display: block;
    padding-top: 56.25%; }

.post-content {
  display: -webkit-flex;
  display: flex;
  padding: 16px; }

.post-text-content {
  line-height: 1.4;
  -webkit-flex: 1;
  flex: 1; }
  .post-text-content p {
    margin: 0;
    color: #666;
    max-width: 550px; }

.post-avatar {
  border-radius: 20px;
  margin-right: 16px;
  object-fit: cover; }

.post-title {
  display: -webkit-flex;
  display: flex;
  line-height: 1.5;
  margin-bottom: 6px;
  font-size: 1rem; }

.post-heading {
  -webkit-flex: 1;
  flex: 1;
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-weight: bold; }

.post-time {
  color: #888; }

.posts-alert {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  right: 0;
  height: 44px;
  background: radial-gradient(ellipse farthest-side at 50% 0, rgba(26, 56, 218, 0.47), rgba(26, 56, 218, 0));
  pointer-events: none;
  will-change: opacity;
  opacity: 0;
  transition: opacity 0.5s ease-in-out; }
  .posts-alert.active {
    opacity: 1; }

.toasts {
  position: absolute;
  z-index: 3;
  bottom: 0;
  left: 0;
  right: 0; }
  @media (min-width: 640px) {
    .toasts {
      bottom: 30px;
      left: 30px;
      right: auto; } }

.toast {
  color: #fff;
  background: #323232;
  padding: 14px 24px;
  padding-right: 0;
  opacity: 0;
  display: -webkit-flex;
  display: flex; }
  @media (min-width: 640px) {
    .toast {
      min-width: 288px;
      max-width: 568px;
      border-radius: 2px; } }
  .toast .toast-content {
    margin-right: 48px;
    -webkit-flex: 1;
    flex: 1; }
  .toast button {
    margin: 0;
    margin-right: 24px;
    color: #FF5252;
    text-transform: uppercase;
    min-width: -webkit-min-content;
    min-width: -moz-min-content;
    min-width: min-content; }
    .toast button:focus {
      outline: none;
      color: #fff; }
